import { fetchFactory, URL, fixtures } from './utils'

var name = 'settings'

export default fetchFactory({
  url: URL[name],
  fixture: fixtures[name],
  parser: parseSettings,
})

/**
 *  
 * @param {number} phone     
 * @param {string} email  email  
 * @param {string} gravatar_email  email     http://gravatar.com
 * @param {string} gravatar_hash       http://gravatar.com
 * @param <Object{boolean}> notifyBy      
 */
export class Settings {
  constructor(data) {
    this.phone = data.phone
    this.email = data.email
    this.gravatar_email = data.gravator_email
    this.gravatar_hash = data.gravator_hash

    // TODO: notifyBy on server side
    this.notifyBy = {
      email: false,
      phone: false,
      gravatar_email: false,
    }
  }
}

/**
 * @param {string} message  
 * @param {Settings} data   
 */
function parseSettings({ data }) {
  return new Settings(data)
}



