import { fetchFactory, URL, fixtures } from './utils'

var name = 'container-info'

export default fetchFactory({
  url: URL[name],
  fixture: fixtures[name],
  parser: parseContainerInfo,
})

/**
 *       
 */
const OPERATIONS = {
  '': 'weighing',
  '': 'nomination',
  '': 'disinfection',
  '': 'inspection',
  '': 'stuffing',
  ' ': 'lathing',
  '': 'MIDK',
  '': 'disconnection',
  ' ': 'delivery',
  '': 'seal',
  '': 'connection',
  '': 'arrival',
  '': 'washing',
  '': 'unstuffing',
  '': 'expense',
  '': 'supply',
}

/**
 *   
 * @param {unixtime} date    
 * @param {string} oper   
 * @param {string} transport   ,    () 
 * @param {string} kontr   

 * @param {string} number   
 * @param {string} type   
 * @param {string} size   
 * @param {boolean} load   
 */
export class ContainerInfo {
  constructor(data) {

    this.date = data.date * 1000
    this.operation = OPERATIONS[data.oper]
    this.transport = data.transport
    this.kontr = data.kontr

    // this.number = data.number
    // this.type = data.type.id
    // this.size = data.size.id
    // this.load = data.load.id ? 1 : 0
  }
}

/**
 * @param {string} message    
 * @param <array{ContainerInfo}> data -   
 */
function parseContainerInfo({ data }) {
  return {
    message: data.message,
    data: data.table.map( item => new ContainerInfo(item) ),
  }
}



