import { fetchFactory, URL, fixtures } from './utils'

var name = 'profile'

export default fetchFactory({
  url: URL[name],
  fixture: fixtures[name],
  parser: parseProfile,
})

/**
 *    
 * @param {string} name   
 * @param {string} full_name    
 * @param {string} INN   
 * @param {string} KPP   
 * @param {string} OKPO   
 * @param {string} Jur_address    
 * @param {string} Fact_address    
 * @param {string} Phone   
 * @param {string} bank_account     
 * @param {string} corr_account    
 * @param {string} Bank_name       
 * @param {string} BIK   
 * @param {string} Bank_address   
 */
export class Profile {
  constructor(data) {
    this.name = data.name
    this.full_name = data.full_name
    this.INN = data.INN
    this.KPP = data.KPP
    this.OKPO = data.OKPO
    this.Jur_address = data.Jur_adress
    this.Fact_address = data.Fact_adress
    this.Phone = data.Phone
    this.bank_account = data.bank_account
    this.corr_account = data.corr_account
    this.Bank_name = data.Bank_name
    this.BIK = data.BIK
    this.Bank_address = data.Bank_adress
  }
}

/**
 * @param {string} message  
 * @param {Profile} data     
 */
function parseProfile({ data }) {
  return {
    message: data.message,
    data: new Profile(data.table.tbody),
  }
}



