import { randomDate, randomNumb, randomChar } from './utils'

export default { login, logout }

/**
 * @param {string} secret     
 * @param {string} message  
 * @param {boolean} ready_check,    
 * @param {string} login   ( ) 
 * @param {dateTime} create_at      
 * @param {boolean} check_cnt,         
 * @param {boolean} check_doc,          
 * @param {boolean} check_zayv,         
 * @param {string} name_expeditor    (),  
 */
export function login() {
  return new Promise( resolve => resolve({
    secret: randomChar(30),
    message: ' ',
    ready_check: randomNumb % 2,
    create_at: randomDate(),
    login: 'demo',
    check_cnt: randomNumb % 2,
    check_doc: randomNumb % 2,
    check_zayav: randomNumb % 2,
    name_expeditor: 'Demo name',
  }))
}

/**
 * @param {string} message  
 */
export function logout() {
  return new Promise( resolve => resolve({
    message: ' ',
  }))
}



