import { fetchFactory, URL, fixtures } from './utils'

var name = 'advance'

export default fetchFactory({
  url: URL[name],
  fixture: fixtures[name],
  parser: parseAdvanceInfo,
})

/**
 *  
 * @param {unixtime} date   
 * @param {number} summa   
 */
export class Advance {
  constructor(data) {
    this.date = data.date * 1000
    this.summa = data.summa
  }
}

/**
 *    
 * @param {string} number   
 * @param {unixtime} date    (  )
 * @param {number} summa    (      ,    ,      )
 */
export class Debt {
  constructor(data) {
    this.number = data.number
    this.date = data.date * 1000
    this.summa = data.summa
  }
}

/**
 *   ,   ,     
 * @param {string} message    
 * @param {number} summa   
 * @param {number} summa_bill      
 * @param <array{Advance}> advance   
 * @param <array{Debt}> debt     
 */
export function parseAdvanceInfo({ data }) {
  return {
    message: data.message,
    summa: data.summa,
    summa_bill: data.summa_bill,
    advance: data.tables.advance.map( item => new Advance(item) ),
    debt: data.tables.debt.map( item => new Debt(item) ),
  }
}



