import { randomNumb } from './utils'

/**
 *    
 * @param {string} name   
 * @param {string} full_name    
 * @param {string} INN   
 * @param {string} KPP   
 * @param {string} OKPO   
 * @param {string} Jur_address    
 * @param {string} Fact_address    
 * @param {string} Phone   
 * @param {string} bank_account     
 * @param {string} corr_account    
 * @param {string} Bank_name       
 * @param {string} BIK   
 * @param {string} Bank_address   
 */
export class Profile {
  constructor() {
    this.name = '   '
    this.full_name = ' "        "'
    this.INN = randomNumb(9)
    this.KPP = randomNumb(8)
    this.OKPO = randomNumb(8)
    this.Jur_address = `${randomNumb(6)}, . ,  ., .${randomNumb(2)}`
    this.Fact_address = `${randomNumb(6)}, . ,  , .${randomNumb(8)}`
    this.Phone = `${randomNumb(3)}-${randomNumb(2)}-${randomNumb(2)}, ${randomNumb(11)}`
    this.bank_account = randomNumb(20)
    this.corr_account = randomNumb(20)
    this.Bank_name = '"Bank of Switzerland"'
    this.BIK = randomNumb(9)
    this.Bank_address = ' .'
  }
}

/**
 * @param {string} message  
 * @param {Profile} data     
 */
export default () => new Promise( resolve => resolve({
  message: 'Fixture profile',
  data: new Profile(),
}))



